* {
    box-sizing: border-box;
    color: #777;
}

html, body {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    min-height: 100%;
    max-height: 100%;
}

:root {
    --text-pad-size: 
}

.nav ul {
    display: flex;
    list-style-type: none;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
}

.nav li {
    padding: 0 0 0 0;
    margin: 0 0 0 0;
}

.nav a {
    background-color: rgba(0, 0, 0, 0);
    transition-duration: 500ms;
    font-size: 25px;
    display: inline-block;
    flex-wrap: nowrap;
    padding: .8em;
    margin: 0 4em;
    text-decoration: none;
}

.nav a:hover {
    background-color: rgba(0, 0, 0, 0.5);
    transition-duration: 350ms;
}

.nav-header {
    width: auto;
    background-color: rgba(0, 0, 0, 0.6);
    padding: .7em;
    width: auto;
}

.foot {
    position: absolute;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 1em;
    margin: 0;
}

.clicker {
    background-color: rgba(0,50,0,0.4);
    width: fit-content;
    margin: 2em 1.5em;
    padding: 10px;
    text-align: center;
}

.clicker code {
    font-size: 15px;
    padding: .2em;
    color: blue;
}

.clicker p {
    color: black;
}

.clicker button {
    border: none;
    padding: 10px;
    color: white;
    background-color: rgba(0, 0, 0, 0.7);
    transition-duration: 250ms;
}

.clicker button:hover {
    filter: invert(100%);
    cursor: pointer;
    transition-duration: 250ms;
}

.foot ul {
    list-style: none;
    width: fit-content;
    margin: 0;
    padding: 0;
}

.foot .copyright {
    font-size: 12px;
    text-align: left;
}

p {
    margin: 1em;
}

@media only screen and (max-width: 915px) {
    .nav a {
        margin: 0 2.5em;
    }
    .nav-header {
        padding: .7em 1em;
    }
}

@media only screen and (max-width: 800px) {
    .nav a {
        margin: 0 0.5em;
        padding: .7em;
    }
    .nav-header {
        padding: .7em 1em;
    }
}

@media only screen and (max-width: 320px) {
    .nav a {
        margin: 0 0.2em;
        padding: .6em;
    }
    .nav-header {
        padding: .7em 1em;
    }
}